import React, { Component } from 'react'
import {connect} from 'dva'
import {Button, List, Select, Skeleton, Tag, Empty, Spin} from 'antd'
import {Link} from 'dva/router'
import UtilGlobal from '../../utils/global'


import BaseForm from '@/components/BaseForm'
import BaseContent from '@/components/BaseContent'
import TagSelect from '@/components/TagSelect'

import './index.css'

const {basePath} = UtilGlobal;
const Option = Select.Option;

const QuestionItems = ({items, history}) =>  {
  
  const edit = (id) => {
    history.push({
      pathname: `${basePath}/main/editQuestions`,
      search: `?id=${id}`,
      state:{
        parent:'',
        child:'',
        id:'main-editQuestions'
      }
    })
  }
  return (
    <List
        className="demo-loadmore-list"
        itemLayout="horizontal"
        dataSource={items}
        renderItem={item => (
          <List.Item actions={[<a href="javascript:;" onClick={() => edit(item.questions_id)}></a>]}>
            <Skeleton avatar title={false} loading={item.loading} active>
              <Link to={{
                pathname: `${basePath}/main/questions/detail`,
                search: `?id=${item.questions_id}`,
                state:{
                  parent:'',
                  child:'',
                  id:'main-questionsDetail'
                }
              }}>
              <List.Item.Meta
                title={item.title}
              />
              <div style={{marginTop: 10}}>
                <div style={{marginBottom: 10}}>
                  <Tag color="blue">{item.questions_type_text}</Tag>
                  <Tag color="geekblue">{item.subject_text}</Tag>
                  <Tag color="orange">{item.exam_name}</Tag>
                </div>
                <span >{item.user_name} </span>
              </div>
              </Link>
            </Skeleton>
          </List.Item>
        )}
      />
  )
}

let mapState = store => {
  return {
    ...store.watchQuestions,
    ...store.main,
    loading: store.loading.global
  }
}
@connect(mapState)
class WatchQuestions extends Component {
  constructor (props) {
    super(props)
    this.state = {
      subject_id: '',
      questions_type_id: '',
      exam_id: ''
    }
  }

  getQuestions (filter = {}) {
    this.props.dispatch({type:'watchQuestions/getQuestions', filter})
  }

  componentDidMount(){
    this.getQuestions()
    this.props.dispatch({type:'watchQuestions/getAllSubject'})
    this.props.dispatch({type:'watchQuestions/getQuestionsType'})
    this.props.dispatch({type:'watchQuestions/getAllExamType'})
  }


  showAnswer = (index) => {
    let {questions} = this.props;
    questions[index].showAnswer = !questions[index].showAnswer;
    this.props.dispatch({type:'watchQuestions/changeState',questions:[...questions]});
  }

  handleFormSubmit = (res) => {
    this.getQuestions(res)
  }

  renderForm () {
    // const actionsTextMap = {
    //   expandText: '',
    //   collapseText: '',
    //   selectAllText: '',
    // };
    const {subject, questionsType, examType} = this.props
    return (
      <BaseForm
        rowItems={[
          {
            label: '',
            field: 'subject_id',
            formItemLayout: {
              labelCol: { span: 2 },
              wrapperCol: { span: 22 }
            },
            content: (
              <TagSelect isRadio>
                {subject.map((item, index) => <TagSelect.Option value={item.subject_id} key={index}>{item.subject_text}</TagSelect.Option>)}
              </TagSelect>
            ),
          }
        ]}
        items={[
          {
            label: '',
            field: 'exam_id',
            content: (
              <Select>
                {examType.map((item, index) => <Option value={item.exam_id} key={index}>{item.exam_name}</Option>)}
              </Select>
            ),
          },
          {
            label: '',
            field: 'questions_type_id',
            content: (
              <Select>
                {questionsType.map((item, index) => <Option value={item.questions_type_id} key={index}>{item.questions_type_text}</Option>)}
              </Select>
            ),
          },
          {
            isFormItem: false,
            content: (
                <Button
                    type="primary"
                    icon="search"
                    htmlType="submit"
                    style={{
                        margin: '3px 0 0 50px',
                    }}
                >
                </Button>
            ),
          }
        ]}
        onSubmit={this.handleFormSubmit}
      />
    )
  }

  render() {
    let {questions} = this.props;
    return <div>
      <Spin spinning={this.props.loading} className="loading" />
      <BaseContent>
        {this.renderForm()}
      </BaseContent>
      <BaseContent>
        {questions.length >= 1 ? <QuestionItems  {...this.props} items={questions}/> : <Empty />}
      </BaseContent>
      </div>
  }
  
}
export default WatchQuestions
